@import url(https://fonts.googleapis.com/css?family=Cabin:400,700|Raleway:400,700&amp;display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

header {
  align-items: left;
  background: #f8f8f8;
  border-bottom: 1px solid #d2d2d2;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  height: 60px;
  justify-content: left;
  left: 0;
  overflow: hidden;
  padding-left: 1rem;
  position: -webkit-sticky;
  position: sticky;
  right: 0;
  top: -40px;
  -webkit-transition: 0.3s all;
  transition: 0.3s all;
  z-index: 10; }
  header h1,
  header h2 {
    margin: 0;
    -webkit-transform: translateY(0.875rem);
            transform: translateY(0.875rem);
    -webkit-transition: 0.3s all;
    transition: 0.3s all;
    padding: 0; }
  header h1 {
    margin-bottom: 10px; }
  header h2 {
    font-size: 1rem;
    margin-top: 0;
    padding: 0;
    -webkit-transition-timing-function: ease-in-out;
            transition-timing-function: ease-in-out; }
  header .contact {
    bottom: 0;
    font-size: 0.625rem;
    line-height: 0.625rem;
    position: absolute;
    right: 0; }
    header .contact .text,
    header .contact .icons {
      padding-bottom: 0.1rem;
      padding-right: 1rem;
      text-align: right;
      -webkit-transition: 0.3s all;
      transition: 0.3s all; }
    header .contact .icons {
      -webkit-transform: translateY(2rem);
              transform: translateY(2rem);
      -webkit-transition-timing-function: ease-in-out;
              transition-timing-function: ease-in-out; }

.scrolled header h1 {
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.scrolled header h2 {
  -webkit-transform: translateY(-0.5rem);
          transform: translateY(-0.5rem); }

.scrolled header .text {
  -webkit-transform: translateY(-1rem);
          transform: translateY(-1rem); }

.scrolled header .icons {
  -webkit-transform: translateY(0);
          transform: translateY(0); }

.container,
section {
  display: flex;
  flex-direction: row;
  position: relative; }

.container {
  flex-wrap: wrap;
  margin: 0 auto;
  max-width: 1440px; }
  .container > div {
    flex-basis: 75%; }
  .container > aside {
    flex-basis: 25%; }

.contents {
  position: -webkit-sticky;
  position: sticky;
  top: 90px; }

aside .contents {
  overflow: hidden;
  top: 0; }

aside .details {
  background: #f8f8f8;
  margin-left: auto;
  margin-top: 2.75rem;
  padding: 2rem; }

section {
  margin-bottom: 4rem;
  padding: 0 1rem 2rem; }
  section article {
    flex-shrink: 0;
    flex-basis: 34%;
    padding-bottom: 4rem; }
    section article .contents {
      padding: 0 1rem 0 0; }
  section ul {
    border-bottom: 1px solid #d2d2d2;
    margin: 0;
    padding-bottom: 4rem;
    padding-right: 2rem; }
  section li {
    margin-bottom: 1rem; }

#scroll-tracker {
  height: 1px; }

body {
  font-family: "Raleway", sans-serif;
  line-height: 1.5rem; }

h1,
h2,
h3,
h4 {
  font-family: "Cabin", sans-serif;
  font-weight: 400;
  line-height: normal;
  line-height: initial;
  margin-top: 0;
  padding: 0; }
  h1 strong,
  h2 strong,
  h3 strong,
  h4 strong {
    font-weight: 700; }

h3 {
  margin-bottom: 1rem; }

h4 {
  margin-bottom: 0; }

p {
  font-size: 0.875rem;
  margin-top: 0; }

a {
  color: #8d8d8d;
  text-decoration: none; }
  a:hover {
    color: tomato; }

.group-title {
  background: white;
  display: inline-block;
  font-weight: 700;
  margin: 0 0 1rem 1rem;
  padding: 1rem 0;
  position: -webkit-sticky;
  position: sticky;
  top: 20px;
  width: 33%;
  z-index: 1; }

.meta {
  font-size: 0.625rem;
  line-height: 1rem; }

blockquote {
  border-color: #d2d2d2;
  border-style: solid;
  border-width: 0 0 1px;
  line-height: 2rem;
  max-width: 1084px;
  margin: 0 auto 4rem;
  padding: 4rem; }

footer {
  background: #f8f8f8;
  border-top: 1px solid #d2d2d2;
  padding: 1rem;
  text-align: center; }
  footer p {
    font-size: 0.625rem;
    margin: 0; }

